
.flexy{
    display:flex;
    flex-direction: row;
     gap: 1rem;
     padding: 1rem;
   
}

@media screen and (max-width: 50rem){
    .flexy{
        display:flex;
        flex-direction: column;
         gap: 1rem;
         padding: 1rem;
         align-items: center;
       
    }
}


figure.snip {
  font-family: 'Raleway', Arial, sans-serif;
  position: relative;
  overflow: hidden;
  margin: 10px;
  min-width: 220px;
  max-width: 310px;
  width: 100%;
  color: #333;
  text-align: left;
  box-shadow: none !important;
 
}

figure.snip img {
  max-width: 100%;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  margin-bottom: 15px;
  display: inline-block;
  z-index: 1;
  position: relative;
}
figure.snip blockquote {
  margin: 0;
  display: block;
  border-radius: 8px;
  position: relative;
  background-color: #fafafa;
  padding: 30px 50px 65px 50px;
  font-size: 0.8em;
  font-weight: 500;
  margin: 0 0 -50px;
  line-height: 1.6em;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
}
figure.snip blockquote:before,
figure.snip blockquote:after {
  font-family: 'FontAwesome';
  content: "\201C";
  position: absolute;
  font-size: 50px;
  opacity: 0.3;
  font-style: normal;
}
figure.snip blockquote:before {
  top: 35px;
  left: 20px;
}
figure.snip blockquote:after {
  content: "\201D";
  right: 20px;
  bottom: 35px;
}
figure.snip .author {
  margin: 0;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
}
figure.snip .author h5 {
  opacity: 0.8;
  margin: 0;
  font-weight: 800;
}
figure.snip .author h5 span {
  font-weight: 400;
  text-transform: none;
  display: block;
}


@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Raleway:400,800);
h2{
  text-align: center;
}